body {
  font-family: 'Open Sans', sans-serif;
  font-size: 15px; }

h1, h2, h3, h4, h5, h6 {
  color: #ff631e; }

.wrapper::before, .row::before {
  display: table;
  content: ''; }

.wrapper::after, .row::after {
  display: table;
  clear: both;
  content: ''; }

.container {
  max-width: 960px;
  width: 100%;
  margin: 0 auto; }

header {
  width: 100%;
  background-color: rgba(225, 225, 46, 0.5);
  text-align: center; }
  header .brand img {
    width: 100%;
    max-height: 175px;
    background-size: contain; }

.feat_wrapper {
  width: 70%;
  margin: 0 auto;
  height: 100%; }
  .feat_wrapper .feature {
    height: 100%; }
  .feat_wrapper .inner .title {
    text-align: center; }
  .feat_wrapper .inner .content {
    width: 80%;
    margin: 0 auto;
    line-height: 1.4;
    color: #3d3d3d; }
    .feat_wrapper .inner .content ul {
      margin: 10px; }
  .feat_wrapper .inner .image {
    width: 100%;
    height: 135px;
    line-height: 135px;
    position: relative;
    text-align: center;
    cursor: pointer; }
    .feat_wrapper .inner .image .overlay {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      background-color: rgba(0, 0, 0, 0.5);
      transition: opacity 0.3s ease; }
    .feat_wrapper .inner .image.img-1 {
      background-size: contain;
      background-repeat: repeat;
      background-image: url("https://patterns.forcir.com/v1/1"); }
    .feat_wrapper .inner .image.img-2 {
      background-size: contain;
      background-repeat: repeat;
      background-image: url("https://patterns.forcir.com/v1/2"); }
    .feat_wrapper .inner .image.img-3 {
      background-size: contain;
      background-repeat: repeat;
      background-image: url("https://patterns.forcir.com/v1/3"); }
    .feat_wrapper .inner .image:hover .overlay {
      opacity: 1; }
    .feat_wrapper .inner .image:hover .number {
      opacity: 0; }
    .feat_wrapper .inner .image h1 {
      color: #c8c8c8;
      font-size: 18px;
      padding: 0;
      margin: 0; }
    .feat_wrapper .inner .image .number {
      text-align: center;
      width: 40px;
      height: 40px;
      background-color: #6f6f6f;
      color: #c8c8c8;
      line-height: 40px;
      font-size: 16px;
      font-weight: bold;
      border-radius: 100%;
      margin: 0 auto;
      position: relative;
      top: calc(50% - 20px);
      opacity: 1;
      transition: opacity 0.3s ease; }

.banner_wrapper {
  background-color: rgba(0, 0, 0, 0.8);
  padding-top: 5px; }
  .banner_wrapper .img img {
    max-height: 100px;
    background-size: cover; }
  .banner_wrapper .content {
    height: 100px;
    display: table-cell; }
    .banner_wrapper .content h1 {
      color: #FFF; }

.contact_wrapper {
  text-align: center; }
  .contact_wrapper .input {
    margin-bottom: 15px; }
    .contact_wrapper .input .text, .contact_wrapper .input .textarea {
      width: 40%;
      border-radius: 3px;
      border: 1px solid #5b5b5b; }
      .contact_wrapper .input .text:focus, .contact_wrapper .input .text:active, .contact_wrapper .input .textarea:focus, .contact_wrapper .input .textarea:active {
        outline: 0; }
    .contact_wrapper .input .text {
      height: 30px;
      padding-left: 10px; }
    .contact_wrapper .input .textarea {
      height: 200px;
      padding-left: 8px;
      padding-top: 5px; }

@media only screen and (max-width: 40rem) {
  .feat_wrapper {
    width: 100% !important; }
  .banner_wrapper {
    text-align: center; } }

@media only screen and (max-width: 64rem) {
  .banner_wrapper {
    text-align: center; }
  .contact_wrapper .input .text, .contact_wrapper .input .textarea {
    width: 80%; } }

@media only screen and (min-width: 64.063rem) {
  .feat_wrapper {
    width: 100% !important; } }
